/* account page css start */
.account-page 
  @extend %d-flex 
  min-height: 100vh
  @extend %position-relative 
  z-index: 1
  +media(991px)
    padding: rem(100px) rem(50px) rem(50px) rem(50px)
  +media(575px)
    padding: rem(100px) rem(15px) rem(15px) rem(15px)
  .content-wrapper 
    @extend %position-relative 
    width: 50% 
    @extend %base-color-two 
    padding: rem(50px)
    @extend %d-flex 
    flex-flow: column
    justify-content: space-between
    @extend %z-index-p
    +media(991px)
      padding: rem(30px)
      position: absolute
      top: 0
      left: 0 
      width: 100% 
      height: 100% 
      z-index: -1
      text-align: center
    .overlay-img 
      @extend %position-absolute 
      top: 0 
      left: 0
      @extend %w-100 
      @extend %h-100 
      @extend %z-index-c
      img 
        @extend %w-100 
        @extend %h-100 
        @extend %obj-fit 
        opacity: 0.25 
    .line-img 
      @extend %position-absolute 
      top: 0 
      left: 0
      @extend %w-100 
      @extend %h-100 
      @extend %z-index-c
      @extend %d-flex
      @extend %align-center 
      opacity: 0.35 
    .content 
      max-width: 500px 
      +media(991px)
        display: none
      .title 
        @extend %text-white 
        font-size: rem(48px) 
        +media(1399px)
          font-size: rem(42px)
        +media(1199px)
          font-size: rem(36px)
    .el-img 
      img 
        max-width: 400px 
    .logo 
      img 
        max-width: 215px 
        max-height: 65px 
  .form-wrapper 
    width: 50% 
    padding: rem(100px) rem(100px) rem(30px) rem(100px)
    @extend %d-flex 
    flex-flow: column
    justify-content: space-between
    @extend %position-relative 
    z-index: 2 
    @extend %bg-white 
    +media(1199px)
      padding: rem(50px) rem(50px) rem(30px) rem(50px)
    +media(991px)
      width: 100% 
    +media(575px)
      padding: rem(20px)
    .inner-wrapper
      max-width: 550px
      margin-bottom: rem(30px)
      margin-left: auto 
      margin-right: auto
  // &.registration-page 
  //   .inner-wrapper 
  .copy-right-text 
    +media(991px)
      text-align: center 


.account-form 
  margin-top: rem(45px)
/* account page css end */