/* how work section css start */
.work-wrapper 
  div[class*="col"]
    @extend %position-relative 
    &:last-child
      &::after 
        display: none
    &:nth-child(2)
      &::after 
        +transform(scaleY(-1) rotate(38deg))
    &::after 
      @extend %position-absolute 
      content: '' 
      top: 38px
      left: 380px 
      width: 150px
      height: 100px
      background-size: 150px 100px 
      background-repeat: no-repeat
      background-image: url('../images/how-work/arrow.png') 
      +transform(rotate(38deg))
      opacity: 0.1
      +media(1399px)
        left: 310px 
      +media(1199px)
        left: 245px 
      +media(991px)
        display: none 

.work-item 
  @extend %text-center 
  padding: 0 rem(30px)
  .work-img 
    @extend %position-relative
    width: 150px
    height: 150px 
    @extend %bs-50 
    @extend %d-flex 
    @extend %align-center
    @extend %justify-center 
    margin-left: auto 
    margin-right: auto 
    margin-bottom: rem(25px)
    @extend %z-index-p
    &::before
      @extend %position-absolute
      content: '' 
      top: 0
      left: 0
      @extend %w-100 
      @extend %h-100 
      @extend %z-index-c
      background: radial-gradient(100% 244.46% at 0% 0%, #CCFF00 0%, #FF027C 100%), radial-gradient(50% 122.23% at 50% 50%, #9AA4FF 0%, #306C00 100%), radial-gradient(100.45% 245.58% at 0% 0%, #000AFE 0%, #70FF00 100%), linear-gradient(127.43deg, #7B0007 0%, #8F73FF 100%)
      background-blend-mode: lighten, color-dodge, difference, normal 
      @extend %bs-50 
      opacity: 0.25
    img
      max-height: 95px
      max-width: 95px
  .title 
    margin-bottom: rem(10px)
/* how work section css end */