/* banner section css start */
.banner-section
  padding-top: rem(250px)
  padding-bottom: rem(150px)
  @extend %base-color-two
  @extend %position-relative
  @extend %z-index-p
  +media(1199px)
    padding-top: rem(145px)
    padding-bottom: rem(85px)
  &::before
    position: absolute
    content: ''
    bottom: -70px
    left: 45px
    height: 75%
    width: 300px
    background: #ffffff
    background: -webkit-linear-gradient(to top, #ffffff, rgba(255, 255, 255, 0.15))
    background: linear-gradient(to top, #ffffff, rgba(255, 255, 255, 0.15))
    z-index: -1
    transform: rotate(45deg)
    opacity: 0.035
  &::after
    position: absolute
    content: ''
    top: -70px
    right: 45px
    height: 75%
    width: 400px
    background: #ffffff
    background: -webkit-linear-gradient(to top, #ffffff, rgba(255, 255, 255, 0.15))
    background: linear-gradient(to top, #ffffff, rgba(255, 255, 255, 0.15))
    z-index: -1
    transform: rotate(45deg)
    opacity: 0.035
  .banner-shade
    @extend %position-absolute
    top: 0
    left: 0
    @extend %w-100
    @extend %h-100
    @extend %z-index-c
    opacity: 0.2
    img
      @extend %w-100
      @extend %h-100
      @extend %obj-fit
  .banner-top-title
    font-size: rem(18px)
    @extend %text-white
    span
      @extend %text-base
  .banner-title
    font-size: rem(56px)
    @extend %text-white
    +media(1399px)
      font-size: rem(48px)
    +media(1199px)
      font-size: rem(42px)
    +media(575px)
      font-size: rem(36px)

.banner-video-btn
  @extend %text-white
  @extend %d-inline-flex
  @extend %align-center
  padding-left: 6px
  &:hover
    @extend %text-white
  i
    width: 55px
    height: 55px
    @extend %base-color
    @extend %bs-50
    @extend %d-flex
    @extend %align-center
    @extend %justify-center
    @extend %text-white
    box-shadow: 0 0 0 6px rgba($base-color, 0.25)
  span
    padding-left: rem(20px)

.banner-transfer
  @extend %base-color-two
  padding: rem(20px)
  @extend %bs-8
  box-shadow: 0 10px 25px rgba(#000, 0.15)
  @extend %position-relative
  @extend %z-index-p
  &::before
    @extend %position-absolute
    content: ''
    top: 0
    left: 0
    @extend %w-100
    @extend %h-100
    background-color: #000
    opacity: 0.25
    @extend %z-index-c
    @extend %bs-8

.custom-transfer-field
  @extend %position-relative
  .caption
    @extend %position-absolute
    top: 10px
    left: 20px
    font-size: rem(12px)
  .form-control
    border: none
    height: 70px
    font-size: rem(18px)
    padding-top: rem(30px)
    +placeholder(lighten(#000, 70%))
    // background-color: rgba(#fff, 0.05) 
    // border: 1px solid rgba(#fff, 0.15)
    // color: #fff !important
  .country-select
    @extend %position-absolute
    top: 0
    right: -2px
    width: 105px
    @extend %h-100
    .country-list
      top: 100%
      width: 100%
    &.inside input,
    &.inside input[type=text]
      padding-left: 42px
  #country_selector,
  #country_selector2
    width: 105px
    font-size: rem(18px)
    @extend %h-100
    @extend %text-white
    @extend %base-color
    border: none
    +border-radius(0 5px 5px 0)
  .flag-dropdown
    @extend %w-100
    @extend %h-100
  .selected-flag
    @extend %position-absolute
    top: 0
    left: 0
    width: 100% !important
    @extend %h-100
    .arrow
      border-top-color: #fff
      right: 12px

.transfer-money-calc
  padding: rem(25px) rem(20px) rem(25px) rem(45px)
  @extend %position-relative
  &::before
    @extend %position-absolute
    content: ''
    top: 0
    left: 20px
    width: 2px
    @extend %h-100
    background-color: darken($base-color-two, 10%)
  .transfer-single-calc
    color: darken(#fff, 10%)
    font-size: rem(14px)
    padding: rem(5px) 0
    @extend %position-relative
    i
      @extend %position-absolute
      top: 6px
      left: -33px
      width: 20px
      height: 20px
      @extend %bs-50
      background-color: darken($base-color-two, 10%)
      @extend %d-flex
      @extend %align-center
      @extend %justify-center
      font-size: rem(12px)

/* banner section css end */

/* page banner css start */
.page-banner
  padding-top: rem(150px)
  padding-bottom: rem(60px)
  @extend %position-relative
  @extend %z-index-p
  +media(1199px)
    padding-top: rem(100px)
    padding-bottom: rem(50px)
  &::before
    @extend %position-absolute
    content: ''
    top: 0
    left: 0
    @extend %w-100
    @extend %h-100
    @extend %base-color-two
    opacity: 0.75
    @extend %z-index-c

.page-breadcrumb
  @extend %d-flex
  @extend %align-center
  li
    @extend %text-white
    padding: 0 rem(10px)
    @extend %position-relative
    &:last-child
      &::after
        display: none
    &::after
      @extend %position-absolute
      content: '/'
      top: 0
      right: rem(-3px)
    a
      @extend %text-base
/* page banner css end */