@import ./_reset
/* global css strat */
@import ./_color
@import ./_bg

.main-wrapper 
  flex-grow: 1 
  flex-shrink: 1
  flex-basis: auto 

.section-header
    margin-bottom: rem(55px)
    +media(575px)
        margin-bottom: rem(35px)

.section-title
    font-size: rem(38px)
    +media(1199px)
        font-size: rem(38px)
    +media(991px)
        font-size: rem(32px)
    +media(767px)
        font-size: rem(28px)

.section-cation-title 
  @extend %text-base 
  font-weight: 600 
  @extend %position-relative 
  padding-left: 35px
  padding-right: 35px 
  &::before 
    @extend %position-absolute 
    content: '' 
    top: 50%
    margin-top: -1px 
    left: 0
    width: 25px 
    height: 2px
    @extend %base-color 
  &::after 
    @extend %position-absolute 
    content: '' 
    top: 50%
    margin-top: -1px 
    right: 0
    width: 25px 
    height: 2px
    @extend %base-color 

.section-bg 
    background-color: $section-bg

.gradient-bg 
  @extend %position-relative 
  @extend %z-index-p 
  &::before 
    @extend %positionTwo 
    opacity: 0.175
    @extend %z-index-c 
    background: radial-gradient(100% 244.46% at 0% 0%, #CCFF00 0%, #FF027C 100%), radial-gradient(50% 122.23% at 50% 50%, #9AA4FF 0%, #306C00 100%), radial-gradient(100.45% 245.58% at 0% 0%, #000AFE 0%, #70FF00 100%), linear-gradient(127.43deg, #7B0007 0%, #8F73FF 100%)
    background-blend-mode: lighten, color-dodge, difference, normal

.back-to-top 
    position: fixed
    bottom: 30px 
    right: 30px
    z-index: 9 
    width: 45px 
    height: 45px
    @extend %base-color 
    box-shadow: 0 5px 10px rgba($base-color, 0.45)
    line-height: 45px
    text-align: center
    @extend %bs-50 
    display: none
    color: #fff 
    font-size: rem(24px)
    cursor: pointer

.preloader-holder
  position: fixed
  left: 0px
  top: 0px
  bottom: 0px
  right: 0px
  width: 100%
  height: 100%
  background-color: $body-bg 
  z-index: 9999

.preloader
  width: 100px
  height: 100px
  position: absolute
  left: 50%
  top: 50%
  transform: translateX(-50%) translateY(-50%)
  animation: rotatePreloader 2s infinite ease-in

  div
    position: absolute
    width: 100%
    height: 100%
    opacity: 0

    &:before
      content: ""
      position: absolute
      left: 50%
      top: 0%
      width: 10%
      height: 10%
      @extend %base-color 
      transform: translateX(-50%)
      border-radius: 50%

    &:nth-child(1)
      transform: rotateZ(0deg)
      animation: rotateCircle1 2s infinite linear
      z-index: 9

    &:nth-child(2)
      transform: rotateZ(36deg)
      animation: rotateCircle2 2s infinite linear
      z-index: 8

    &:nth-child(3)
      transform: rotateZ(72deg)
      animation: rotateCircle3 2s infinite linear
      z-index: 7

    &:nth-child(4)
      transform: rotateZ(108deg)
      animation: rotateCircle4 2s infinite linear
      z-index: 6

    &:nth-child(5)
      transform: rotateZ(144deg)
      animation: rotateCircle5 2s infinite linear
      z-index: 5

    &:nth-child(6)
      transform: rotateZ(180deg)
      animation: rotateCircle6 2s infinite linear
      z-index: 4

    &:nth-child(7)
      transform: rotateZ(216deg)
      animation: rotateCircle7 2s infinite linear
      z-index: 3

    &:nth-child(8)
      transform: rotateZ(252deg)
      animation: rotateCircle8 2s infinite linear
      z-index: 2

    &:nth-child(9)
      transform: rotateZ(288deg)
      animation: rotateCircle9 2s infinite linear
      z-index: 1

    &:nth-child(10)
      transform: rotateZ(324deg)
      animation: rotateCircle10 2s infinite linear
      z-index: 0

@keyframes rotatePreloader
  0%
    transform: translateX(-50%) translateY(-50%) rotateZ(0deg)

  100%
    transform: translateX(-50%) translateY(-50%) rotateZ(-360deg)

@keyframes rotateCircle1
  0%
    opacity: 0

  0%
    opacity: 1
    transform: rotateZ(36deg)

  7%
    transform: rotateZ(0deg)

  57%
    transform: rotateZ(0deg)

  100%
    transform: rotateZ(-324deg)
    opacity: 1

