/* testimonial section css start */
.testimonial-item 
  padding: rem(25px)
  @extend %bg-white 
  @extend %bs-8 
  box-shadow: 0 5px 10px rgba(#000, 0.05)
  @extend %position-relative 
  border: 1px solid $border-color 
  &::before
    @extend %position-absolute 
    content: '' 
    top: 0 
    left: 0 
  .testimonial-dsc  
    font-size: rem(16px)
  .client 
    @extend %d-flex 
    @extend %align-center 
    margin-top: rem(15px)
    .thumb 
      width: 60px 
      height: 60px 
      @extend %bs-50 
      @extend %overflow-hidden 
    .content 
      width: calc(100% - 80px)
      padding-left: rem(15px)

.testimonial-slider 
  .slick-list 
    margin: rem(-15px)
  .single-slide 
    padding: rem(15px)
/* testimonial section css end */