/* footer section css start */
.footer-section 
  @extend %base-color-two 
  .footer-top
    padding: rem(70px) 0
  .footer-bottom
    .container
      padding: rem(20px) 0
      border-top: 1px solid rgba(#fff, 0.1)
  p 
    @extend %text-white 
.footer-logo 
  img 
    max-width: 215px 
    max-height: 65px 

.inline-menu 
  @extend %d-flex 
  @extend %align-center 
  margin: rem(-3px) rem(-10px)
  li 
    padding: rem(3px) rem(10px)
    a 
      @extend %text-white 
      @extend %transition 
      &:hover 
        @extend %text-base 

.social-media-links 
  @extend %d-flex 
  @extend %align-center 
  margin: rem(-3px) rem(-10px)
  li 
    padding: rem(3px) rem(10px)
    a
      @extend %text-white 
      @extend %transition 
      &:hover 
        @extend %text-base 
/* footer section css end */