/* faq section css start */
.faq-section 
  @extend %base-color-two
  .title
    margin-bottom: rem(10px)
  .section-title 
    @extend %text-white 
  p 
    color: darken(#fff, 15%)
  .faq-item 
    .title 
      @extend %text-white

.faq-item 
  padding-left: rem(40px)
  .title 
    font-weight: 600 
    @extend %position-relative 
    &::before 
      @extend %position-absolute 
      content: "\f00c"
      top: 3px
      left: -40px
      width: 25px
      height: 25px 
      @extend %base-color 
      @extend %bs-50 
      font-family: 'Line Awesome Free'
      font-weight: 900
      font-size: rem(16px)
      @extend %d-flex 
      @extend %justify-center 
      @extend %align-center 

.faq-wrapper 
  @extend %position-relative 
  padding: rem(20px) rem(25px)
  @extend %bg-white 
  @extend %bs-10 
  @extend %overflow-hidden 
  &::before 
    @extend %position-absolute
    content: '' 
    top: 0 
    left: 0 
    @extend %w-100 
    height: 4px
    @extend %base-color 
  .accordion-body 
    p 
      color: $para-color
/* faq section css end */