/* blog section css start */
.blog-post 
  @extend %bg-white 
  @extend %bs-8 
  box-shadow: 0 6px 20px rgba(#000, 0.1)
  .thumb 
    @extend %bs-8 
    @extend %overflow-hidden 
    height: 275px 
    img 
      @extend %obj-fit 
      @extend %w-100 
      @extend %h-100 
  .content 
    padding: rem(25px)
    .post-date 
      padding: rem(2px) rem(15px)
      background-color: rgba($base-color, 0.15)
      @extend %text-base 
      @extend %bs-5 
      margin-bottom: rem(10px) 
    .blog-btn 
      padding: rem(4px) rem(10px)
      @extend %base-color
      @extend %bs-5 
      @extend %text-white 
/* blog section css end */


/* blog details section css start */
.blog-details-img
  @extend %bs-8
  @extend %overflow-hidden 
  height: 470px 
  img 
    @extend %w-100 
    @extend %h-100 
    @extend %obj-fit 
  
.blog-details-content 
  margin-top: rem(30px)

.blog-widget 
  padding: rem(25px)
  @extend %bg-white 
  border: 1px solid $border-color 
  @extend %bs-8 
  .blog-widget-title 
    font-size: rem(20px)
    margin-bottom: rem(25px)

.short-post + .short-post
  margin-top: rem(20px)

.short-post 
  @extend %d-flex 
  .thumb 
    width: 75px
    height: 65px 
    @extend %bs-5 
    @extend %overflow-hidden 
    img 
      @extend %w-100 
      @extend %h-100 
      @extend %obj-fit 
  .content 
    width: calc(100% - 75px)
    padding-left: rem(15px)
    .title  
      font-size: rem(15px)
      a 
        font-weight: 600 
        display: -webkit-box
        -webkit-line-clamp: 2
        -webkit-box-orient: vertical
        overflow: hidden
        text-overflow: ellipsis

/* blog details section css end */