/* choose us section css start */
.overview-wrapper 
  margin-top: rem(10px)

.overview-item 
  .title 
    font-weight: 700 

.choose-item 
  .title 
    margin-bottom: rem(10px)
    @extend %position-relative 
    padding-left: 30px
    &::before
      @extend %position-absolute
      content: '' 
      top: 3px  
      left: 0
      width: 20px 
      height: 20px 
      background: linear-gradient(to right, $base-color-two, $base-color)
      @extend %bs-50 
/* choose us section css end */