/* contact section css start */
.contact-item 
  padding: rem(20px) rem(170px) rem(20px) rem(20px)
  background-color: $section-bg 
  @extend %bs-8 
  @extend %h-100 
  +media(1399px)
    padding-right: rem(140px)
  +media(1199px)
    padding: rem(20px)
  .icon 
    width: 45px
    height: 45px 
    @extend %bs-8 
    @extend %base-color 
    @extend %text-white 
    @extend %d-flex 
    @extend %align-center 
    @extend %justify-center 
    font-size: rem(24px)
  .content 
    margin-top: rem(40px)
    p 
      a 
        @extend %text-p 
        &:hover 
          @extend %text-base 
/* contact section css end */