.btn 
  padding: rem(12px) rem(30px)
  &.btn-md
    padding: rem(10px) rem(20px)
  &.btn-sm 
    padding: rem(8px) rem(20px)

.main-btn 
  @extend %base-color 
  @extend %text-white
  font-weight: 500 
  &:hover 
    @extend %text-white 
.main-btn2
  @extend %base-color-two
  @extend %text-white
  font-weight: 500 
  &:hover 
    @extend %text-white 